<!-- 路由配置 -->
<template>
  <div>
    <div class="nav">
      <div class="left">
        <!-- 头像 -->
        <img class="avator" src="../assets/avator.png" />
        <!-- 汉堡包分类 -->
        <van-icon name="bars" @click="handleChangeNavbar()" size="0.26rem" class="bars" />
        <span>rodgers 博客</span>
      </div>
      <div class="right">
        <van-search class="input" v-model="value" placeholder="请输入关键词" show-action shape="round" @search="onSearch()">
          <div slot="action" @click="onSearch()">搜索</div>
        </van-search>
        <!-- 全屏显示navbar -->
        <div class="items">
          <router-link :to="item.path" :class="{ item: true, underline: item.checked }" v-for="(item, keys) in navbar || []" :key="item.title">
            <span @click="handlechageItem(keys)">{{ item.title }}</span>
          </router-link>
        </div>
      </div>
    </div>
    <!-- 左侧抽屉 -->
    <van-popup v-model="show" position="left" :style="{ width: '60%', height: '100%' }">
      <div class="navbar" @click="handleChangeNavbar()">
        <div class="NavItem" v-for="(item, keys) in navbar" :key="item.title">
          <router-link :to="item.path" :class="{ item: true, underline: item.checked }"><span @click="handlechageItem(keys)">{{
              item.title
            }}</span></router-link>
        </div>
      </div>
      <!-- 好题精选部分 -->
      <div class="questionList" v-if="this.$route.path === '/DailyQuestion'">
        <van-collapse v-model="index" accordion>
          <van-collapse-item style="paddingLeft:.10rem" v-for="(item, keys) in dailyQuestion.leftList" :key="keys" :title="item.title" :name="keys">
            <div v-for="(child, childKey) in item.list" :key="childKey" :class="{
                active:
                  keys === getCheckList[0] && childKey === getCheckList[1],
                items_question: true
              }" @click="handleChange([keys, childKey])">
              {{ child.subTitle }}
            </div>
          </van-collapse-item>
        </van-collapse>
      </div>
    </van-popup>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import { mapState } from "vuex";
import { CHANGE_NAVBAR, GET_NAVBARDATA } from "../store/mutation-types/navbar";
import { GETNAVBARAPI } from "../store/api/navbar";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      value: "",
      show: false,
      index: 0
    };
  },
  props: ["getCheckList"],
  //监听属性 类似于data概念
  computed: { ...mapState(["navbar"]), ...mapState(["dailyQuestion"]) },
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    onSearch() {
      console.log("搜索");
    },
    handleChangeNavbar() {
      this.show = !this.show;
      this.index = this.$parent.$refs.items.index;
    },
    handlechageItem(key) {
      // 个人GitHub单独处理
      if (key === this.navbar.length - 1) {
        // window.open("https://github.com/weiweixuan", "_blank");
        location.href = "https://github.com/weiweixuan";
        return;
      }
      this.$store.commit(CHANGE_NAVBAR, key);
    },
    // 刷新后，根据pathName配置路由
    reflash() {
      let pathName = location.pathname,
        index = 0;
      if (pathName != "/") {
        index = this.navbar.findIndex(
          item => item.path != "/" && pathName.indexOf(item.path) > -1
        );
        index = index > -1 ? index : 0;
      }
      this.handlechageItem(index);
    },
    handleChange(arr) {
      this.$emit("changeList", arr);
      this.$parent.$refs.items.getContent(arr);
      this.$parent.$refs.items.changeIndex(this.index);
      // 关闭弹窗
      this.show = !this.show;
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    this.$store.dispatch(GET_NAVBARDATA, {
      url: GETNAVBARAPI,
      setCb: this.reflash
    });
  },
  beforeCreate() {}, //生命周期 - 创建之前
  beforemount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {} //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style scoped lang="stylus">
@media screen and (min-width: 1050px) {
  .nav {
    min-width: 360px;
    overflow: hidden;
    position: fixed;
    top: 0;
    left: 0;
    height: 0.74rem;
    width: 100%;
    z-index: 2000;
    box-sizing: border-box;
    background: #fff;
    border-bottom: 0.01rem solid #ddd;
    padding: 0.1rem 0.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .left {
      font-size: 0.24rem;
      display: flex;
      align-items: center;

      .bars {
        display: none;
      }

      .avator {
        width: 0.4rem;
        height: 0.4rem;
        margin-right: 0.1rem;
        border-radius: 50%;
      }
    }

    .right {
      display: flex;
      align-items: center;

      .input {
        margin-right: 0.2rem;
      }
    }
  }

  .navbar {
    display: none;
  }

  .items {
    font-size: 0.14rem;

    .item {
      margin: 0 0.1rem;
      border-bottom: 0.02rem solid transparent;
      text-decoration: none;
      line-height: 0.74rem;
      color: #000;
    }

    .item:hover {
      border-bottom: 0.02rem solid skyblue;
    }

    .item.underline {
      border-bottom: 0.02rem solid skyblue;
    }
  }
}

@media screen and (max-width: 1050px) {
  .nav {
    min-width: 360px;
    overflow: hidden;
    position: fixed;
    top: 0;
    left: 0;
    height: 0.74rem;
    width: 100%;
    z-index: 2000;
    box-sizing: border-box;
    background: #fff;
    border-bottom: 0.01rem solid #ddd;
    padding: 0.1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .left {
      font-size: 0.2rem;
      display: flex;
      align-items: center;

      .bars {
        transform: translateY(0.02rem);
        margin-right: 0.1rem;
      }

      .avator {
        display: none;
      }
    }

    .right {
      display: flex;
      align-items: center;

      .input {
        width: 2.6rem;
      }
    }
  }

  .items {
    display: none;
  }

  .navbar {
    display: block;

    .NavItem {
      font-size: 0.2rem;
      padding: 0.1rem;

      .item {
        margin: 0 0.1rem;
        border-bottom: 0.02rem solid transparent;
        text-decoration: none;
        color: #000;
      }

      .item.underline {
        border-bottom: 0.02rem solid skyblue;
      }
    }
  }

  .questionList {
    margin-top: 0.1rem;
    border-top: 0.1rem solid #3eaf7c;

    .active {
      color: #3EAF7C;
    }

    .items_question {
      font-size: 0.14rem;
      padding: 0.03rem 0;
      cursor: pointer;
      transition: all linear 0.25s;

      &:hover {
        transform: translateX(0.05rem);
      }
    }
  }
}
</style>
