import { CHANGE_NAVBAR, SET_NAVBARDATA } from '../mutation-types/navbar'
console.log(CHANGE_NAVBAR, 'hhhhhhhhhh')
// import $axios from '../../utils/fetch'
export default {
  //切换路由
  [CHANGE_NAVBAR](state, key) {
    state.navbar = state.navbar.map((element, index) => {
      if (index == key) {
        element.checked = true
      } else {
        element.checked = false
      }
      return element
    })
  },
  // 获取路由数据
  [SET_NAVBARDATA](state, data) {
    console.log(data, state, 'data,哈哈哈')
    state.navbar = data;
  }
}
