import { GET_NAVBARDATA, SET_NAVBARDATA } from '../mutation-types/navbar'
import $axios from '../../utils/fetch'
export default {
  async [GET_NAVBARDATA](store, payload) {
    const res = await $axios(payload);
    store.commit(SET_NAVBARDATA, res)
    if (payload.setCb) {
      payload.setCb()
    }
  }
}